import React, { useCallback, useEffect, useMemo, useState } from 'react'
import { styled } from '@mui/material/styles'
import Card from 'components/Card'
import useSystemParams from 'hooks/useSystemParams'
import { useWeb3React } from 'web3-react-core'
import useToast from 'hooks/useToast'
import { useGetTokenBalance } from 'hooks/useManager'
import { toSmallUnits } from 'utils/transformHelper'
import tokens from 'config/constants/tokens'
import { Title } from 'components/CommonComponent'
import { formatWBTC } from 'utils/formatTheValue'
import moment from 'moment'
import { hedgingVersion } from 'config/constants/contants'
import useHashPowerToken from '../../hooks/useHashPowerToken'
import HedgingTable from './HedgingTable'

const HedgingBox = styled('div')`
  width: 100%;
  height: 100%;
  padding-right: 100px;
  overflow: auto;
`

function useHedging({ tokenIndex }: { tokenIndex: number }) {
  const { toastError } = useToast()
  const { active, library, chainId, account } = useWeb3React()

  const { hashPowerTokenList, walletTokenBalance } = useHashPowerToken()
  const { position, ticker } = hashPowerTokenList[tokenIndex] || { position: '', ticker: '' }
  // const [markPrice, setMarkPrice] = useState(0)
  const { instrumentContract, instrumentContract2, observerContract } = useSystemParams()
  const instrument = tokenIndex === 0 ? instrumentContract : instrumentContract2

  const getTokenBalance = useGetTokenBalance(library, active, chainId)
  const [avatarAssetList, setAvatarAssetList] = useState<any>()

  const getPositonBalance = useCallback(async () => {
    const assetList = await getTokenBalance(position)
    if (assetList) {
      setAvatarAssetList(assetList)
    }
  }, [getTokenBalance, position])

  useEffect(() => {
    if (position && account) {
      getPositonBalance()
    }
  }, [account, getPositonBalance, position])
  // useEffect(() => {
  //   queryMarkPrice()
  // }, [queryMarkPrice])

  const outstandingDebts = useMemo(() => {
    return hashPowerTokenList && hashPowerTokenList[tokenIndex] && hashPowerTokenList[tokenIndex].balance
      ? Number(toSmallUnits(`${hashPowerTokenList[tokenIndex].balance}`, 0)) /
          Number(toSmallUnits('1', hashPowerTokenList[tokenIndex].decimals))
      : 0
  }, [hashPowerTokenList, tokenIndex])

  // const [hedgingData, setHedginData] = useState()
  const [tableInfo, setTablInfo] = useState([])

  const markPriceVal = useMemo(() => {
    if (hashPowerTokenList && hashPowerTokenList.length && hashPowerTokenList[tokenIndex].position) {
      return Number(hashPowerTokenList[tokenIndex].position)
        ? formatWBTC(hashPowerTokenList[tokenIndex].tokensValues[hashPowerTokenList[tokenIndex].token])
        : 0
    }
    return 0
  }, [hashPowerTokenList, tokenIndex])

  const hedgingData = useMemo(() => {
    return [
      {
        ticker,
        markPrice: markPriceVal,
        balanceWallet: walletTokenBalance[ticker],
        balanceCollateral: avatarAssetList && avatarAssetList[ticker],
        outstandingDebt: outstandingDebts,
        addIcon: false,
        childTableInfo: tableInfo,
      },
    ]
  }, [avatarAssetList, markPriceVal, outstandingDebts, tableInfo, ticker, walletTokenBalance])

  const getPositionVal = useCallback(
    async (deliveryDate: string) => {
      let result = 0
      try {
        const res = await observerContract.getPosition(
          tokenIndex === 0
            ? '0x68e6958bcb7f2e1e87a872dde341f8cbe7d52bc6'
            : '0x0C10527c06828024CA2c1A993DdB3E922DFb8dE2',
          deliveryDate,
          account,
        )
        if (res && res.size) {
          result = res.size
        }
      } catch (error) {
        if (error) {
          toastError('Failed', (error as Error).message)
        }
      }
      return result
    },
    [account, observerContract, toastError, tokenIndex],
  )

  const getFairPrice = useCallback(
    async (deliveryDate: string) => {
      let result = 0
      try {
        const res = await instrument.inquire(deliveryDate, 0)
        if (res && res.sqrtFairPX96) {
          // TODO
          result = Number(res.sqrtFairPX96 / 2 ** 96) ** 2
        }
      } catch (error) {
        if (error) {
          toastError('Failed', (error as Error).message)
          return 0
        }
      }
      return result
    },
    [instrument, toastError],
  )

  const getExpiries = useCallback(async () => {
    if (position && active && instrument && observerContract) {
      try {
        const txInfo = await instrument.getExpiries()
        if (txInfo) {
          const childTable = []

          for (let i = 0; i < txInfo.length; i++) {
            const _deliveryDate = txInfo[i]
            const _positionVal = await getPositionVal(_deliveryDate)
            const _fairPrice = await getFairPrice(_deliveryDate)
            childTable.push({
              deliveryDate: moment(_deliveryDate * 1000).format('YYYY-MM-DD'),
              fairPrice: _fairPrice.toFixed(tokens.WBTC.decimals),
              netPosition: Number(_positionVal),
              // short: Number(_positionVal) > 0 ? Number(toLargeUnits(_positionVal, 18)) : 0,
              // shortPosition: Number(_positionVal) < 0 ? Number(toLargeUnits(_positionVal, 18)) : 0,
              // F(BTC,20)-WBTC
              actionUrl: `https://testnet.synfutures.com/#/trade/blastsepolia/F(BTC,25)-WBTC-${hedgingVersion}-${moment(
                _deliveryDate * 1000,
              ).format('MMDD')}`,
            })
          }
          setTablInfo(childTable)
        }
      } catch (error) {
        if (error) {
          toastError('Failed', (error as Error).message)
        }
      }
    }
  }, [active, getFairPrice, getPositionVal, instrument, observerContract, position, toastError])

  useEffect(() => {
    getExpiries()
  }, [getExpiries])

  return hedgingData[0]
}

function HedgingPage() {
  const hedging0 = useHedging({ tokenIndex: 0 })
  const hedging1 = useHedging({ tokenIndex: 1 })

  return (
    <HedgingBox>
      <Title>Hedging</Title>
      <Card
        style={{
          paddingTop: '15px',
          paddingLeft: '20px',
          paddingRight: '20px',
          overflow: 'auto',
          height: 'calc(100% - 200px)',
        }}
      >
        <HedgingTable testData={[hedging0, hedging1]} />
      </Card>
    </HedgingBox>
  )
}

export default HedgingPage
